<template>
	<div class="wrapper">
		<div class="menu" ref="menuWrapper">
			<ul>
				<li v-for="(item,index) in goods" class="menu-item" :class="{'current': currentIndex === index}" @click="selectMenu(index)">
					<span class="text"><span v-show="item.type>0" class="icon" :class="classMap[item.type]"></span>{{ item.name }}</span>
				</li>
			</ul>
		</div>
		<div class="food" ref="foodsWrapper">
			<ul>
				<li v-for="item in goods" class="food-list food-list-hook">
					<h1 class="title">{{ item.name }}</h1>
					<ul>
						<li v-for="food in item.foods" class="food-item">
							<div class="icon">
								<img width="57" height="57" :src="food.icon" alt="">
							</div>
							<div class="content">
								<h2 class="name">{{ food.name }}</h2>
								<p v-show="food.description" class="desc">{{ food.description }}</p>
								<div class="extra">
									<span class="count">月售{{ food.sellCount }}份</span>
									<span>好评率{{ food.rating }}%</span>
								</div>
								<div class="price">
									<span class="now">￥{{ food.price }}</span>
									<span v-show="food.oldPrice" class="old">￥{{ food.oldPrice }}</span>
								</div>
							</div>
						</li>
					</ul>
				</li>
			</ul>
		</div>
		<shopcart :delivery-price="seller.deliveryPrice" :min-price="seller.minPrice"></shopcart>
	</div>
</template>

<script type="ecmascript-6">
import BScroll from 'better-scroll'
import shopcart from '@/components/shopcart/shopcart.vue'
	const ERR_OK = 0

	export default {
		props: {
			seller: {
				type: Object
			}
		},
		data() {
			return {
				goods: [],
				listHeight: [],
				scrollY: 0
			}
		},
		computed: {
			currentIndex() {
				for(let i=0; i<this.listHeight.length; i++) {
					let height1 = this.listHeight[i]
					let height2 = this.listHeight[i + 1]
					if(!height2 || (this.scrollY >= height1 && this.scrollY < height2)) {
						return i
					}
				}
				return 0
			}
		},
		created() {
			this.classMap = ['decrease', 'discount', 'special', 'invoice', 'guarantee']

			this.$axios.get('/api/goods')
				.then(response => {
					response = response.data
					if(response.errno == ERR_OK) {
						this.goods = response.data
						this.$nextTick(() => {
							this._initScroll()
							this._calculateHeight()
						})
					}
				})
				.catch(error => {
					console.log(error)
			})
		},
		methods: {
			_initScroll() {
				this.menuScroll = new BScroll(this.$refs.menuWrapper, {click: true})

				this.foodsScroll = new BScroll(this.$refs.foodsWrapper, {probeType: 3})
				this.foodsScroll.on('scroll', (pos) => {
					this.scrollY = Math.abs(Math.round(pos.y))
				})
			},
			_calculateHeight() {
				let foodList = this.$refs.foodsWrapper.querySelectorAll(".food-list-hook")
				let height = 0
				this.listHeight.push(height)
				for(let i=0; i<foodList.length; i++) {
					let item = foodList[i]
					height += item.clientHeight
					this.listHeight.push(height)
				}
			},
			selectMenu(index) {
				let foodList = this.$refs.foodsWrapper.querySelectorAll(".food-list-hook")
				let el = foodList[index]
				this.foodsScroll.scrollToElement(el, 300)
			}
		},
		components: {
			shopcart
		}
	}
</script>

<style rel="stylesheet" lang="less" scoped>

	@import '../../common/style/mixin.less';
	.wrapper {
		display: flex;
		position: absolute;
		top: 174px;
		bottom: 48px;
		width: 100%;
		overflow: hidden;
		.menu {
			flex: 0 0 80px;
			width: 80px;
			background: #f3f5f7;		
			.menu-item {	
				display: table;
				height: 54px;
				width: 56px;
				padding: 0 12px;
				line-height: 14px;
				&.current {
					position: relative;
					z-index: 10;
					margin-top: -1px;
					font-weight: 700;
					background: #fff;
					.text {
						&::after {
							border: none;
						}
					}
				}				
				.icon {
					display: inline-block;
					width: 12px;
					height: 12px;
					background-size: 12px;
					vertical-align: top;
					margin-right: 2px;
					&.decrease {
						.bg-image('../../components/goods/decrease_3');
					}
					&.discount {
						.bg-image('../../components/goods/discount_3');
					}					
					&.guarantee {
						.bg-image('../../components/goods/guarantee_3');
					}					
					&.invoice {
						.bg-image('../../components/goods/invoice_3');
					}
					&.special {
						.bg-image('../../components/goods/special_3');
					}
				}
				.text {
					display: table-cell;
					width: 56px;
					position: relative;
					font-size: 12px;
					line-height: 14px;
					vertical-align: middle;
					color: #000;
					&::after {
						position: absolute;
						bottom: 4px;
						display: block;
						content: '';
						width: 56px;
						height: 1px;
						border-bottom: 1px solid rgba(7, 17, 27, .1);
					}
				}	
			}
		}
		.food {
			flex: 1;
			.title {
				padding-left: 14px;
				height: 26px;
				line-height: 26px;
				border-left: 2px solid #d9dde1;
				font-size: 12px;
				color: rgb(147, 153, 159);
				background: #f3f5f7;
			}
			.food-item {
				display: flex;
				margin: 18px;
				padding-bottom: 18px;
				border-bottom: 1px solid rgba(7, 17, 27, .1);
				&:last-child {
					border: none;
					margin-bottom: 0;
				}
			}
			.icon {
				flex: 0 0 57px;
				margin-right: 10px;
			}
			.content {
				flex: 1;
				.name {
					margin: 2px 0 8px 0;
					height: 14px;
					font-size: 14px;
					line-height: 14px;
					color: rgb(7, 17, 27);
				}
				.desc {
					margin-bottom: 8px;
					line-height: 10px;
					font-size: 10px;
					color: rgb(147, 153, 159);
				}
				.extra {
					line-height: 10px;
					font-size: 10px;
					color: rgb(147, 153, 159);
					.count {
						margin-right: 12px;
					}
				}
				.price {
					font-weight: 700;
					line-height: 24px;
					.now {
						margin-right: 18px;
						font-size: 14px;
						color: rgb(240, 20, 20);
					}
					.old {
						text-decoration: line-through;
						font-size: 10px;
						color: rgb(147, 153, 159);
					}
				}
			}
		}
	}
</style>


